<template>
  <div id="app">
    <b-alert
      variant="danger"
      dismissible
      fade
      :show="error"
      @dismissed="error = false"
    >
      {{ errorMessage }}
    </b-alert>
    <form v-if="hasFormData" class="form bg-light p-3" @submit.prevent>
      <div
        v-for="form in formData.slice(1)"
        :key="form.name"
        class="form-group row"
      >
        <label
          v-if="form.type !== 'checkbox'"
          :for="form.name"
          class="col-2 col-form-label"
        >
          <p>
            {{ form.label }}
          </p>
        </label>
        <div class="col-10">
          <template v-if="form.type === 'password'">
            <input
              :id="form.name"
              v-model.trim="form.value"
              class="form-control input-password"
              :type="form.type"
              :required="form.required"
              :name="form.name"
              :disabled="!form.enabled"
            />
            <a href="javascript:void(0)" class="show-pass"
              ><i class="fa fa-eye-slash"></i
            ></a>
          </template>
          <template v-if="form.type === 'date'">
            <input
              :id="form.name"
              v-model.trim="form.value"
              class="form-control"
              :type="form.type"
              :required="form.required"
              :name="form.name"
              :disabled="!form.enabled"
            />
          </template>
          <template v-if="form.type === 'datetime'">
            <datetime
              v-model.trim.lazy="form.value"
              :type="form.type"
              input-class="form-control"
              :input-id="form.name"
              format="MM/dd/yyyy HH:MM a"
              autocomplete="off"
              :disabled="!form.enabled"
            ></datetime>
          </template>
          <template v-if="form.type === 'paragraph'">
            <input
              :id="form.name"
              v-model.trim="form.value"
              :type="form.type"
              class="form-control"
              :required="form.required"
              readOnly="true"
              :name="form.name"
              :disabled="!form.enabled"
            />
          </template>
          <template v-if="form.type === 'text'">
            <input
              :id="form.name"
              v-model.trim="form.value"
              :type="form.type"
              class="form-control"
              :required="form.required"
              :name="form.name"
              :disabled="!form.enabled"
            />
          </template>
          <template v-if="form.type === 'textarea'">
            <textarea
              :id="form.name"
              v-model.trim="form.value"
              class="form-control"
              :required="form.required"
              :name="form.name"
              :rows="form.rows"
              :cols="form.cols"
              :disabled="!form.enabled"
            ></textarea>
          </template>
          <template v-if="form.type === 'checkbox'" class="custom-control">
            <b-form-checkbox
              :id="form.name"
              v-model.lazy="form.value"
              class="custom-control-input"
              :name="form.name"
              :disabled="!form.enabled"
            >
              {{ form.label }}
            </b-form-checkbox>
          </template>
          <template v-if="form.type === 'select'">
            <multiselect
              v-model.lazy="form.selectedValue"
              :options="form.options"
              :searchable="false"
              :show-labels="false"
              label="label"
              track-by="label"
              :disabled="!form.enabled"
            ></multiselect>
          </template>
          <template v-if="form.type === 'selectsearch'">
            <multiselect
              v-model.lazy="form.value"
              :options="form.options"
              :custom-label="nameWithLang"
              label="label"
              track-by="label"
              :disabled="!form.enabled"
            ></multiselect>
          </template>
          <template v-if="form.type === 'multiselect'"
            ><multiselect
              v-model="form.selectedValues"
              :options="form.options"
              :multiple="true"
              :close-on-select="false"
              :preserve-search="true"
              label="label"
              track-by="label"
              :reset-after="false"
              group-values="groupOptions"
              group-label="groupLabel"
              :group-select="true"
              :hide-selected="true"
              :disabled="!form.enabled"
            ></multiselect>
          </template>
          <template v-if="form.type === 'repeater'">
            <div v-if="hasRepeaterTemplates" class="mx-2 my-1">
              <form-template
                v-for="(template, templateIndex) in repeaterTemplates"
                :key="templateIndex"
                :template-number="templateIndex + 1"
                :template-amount="repeaterTemplatesLength"
                :form-template="template"
                @create="createTemplate"
                @update="updateTemplate"
                @delete="deleteTemplate"
              ></form-template>
            </div>
            <div v-else class="mx-2 my-1">
              <init-form-template
                :init-form-template="repeaterForm"
                @create="createFromInit"
                @set-init-template="setInitTemplate"
              ></init-form-template>
            </div>
          </template>
        </div>
      </div>
    </form>

    <div class="mb-3 row">
      <div class="col-2">
        <button
          class="btn btn-primary mt-2"
          type="submit"
          @click.prevent="submitForm"
        >
          Запиши
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import FormTemplate from './components/FormTemplate.vue'
import InitFormTemplate from './components/InitFormTemplate.vue'

export default {
  name: 'App',
  components: {
    FormTemplate,
    InitFormTemplate,
  },
  data() {
    return {
      formData: [],
      repeaterForm: {},
      repeaterTemplates: [],
      cleanRepeaterForm: {},
      submitData: [],
      counter: 1,
      error: false,
      errorMessage: '',
      testData: [],
    }
  },
  computed: {
    hasFormData() {
      return !this.$lodash.isEmpty(this.formData)
    },
    hasRepeaterTemplates() {
      return !this.$lodash.isEmpty(this.repeaterTemplates)
    },
    repeaterTemplatesLength() {
      return this.repeaterTemplates.length
    },
  },
  mounted() {
    this.getData()
  },
  methods: {
    async getData() {
      const apiUrl = !this.$lodash.isEmpty(window.jsonData)
        ? window.jsonData
        : {
            jsonUrlControls:
              'https://corporate.orak365.net/SmartRegistry/Users/GetTestRepeater',
            jsonUrlCreateEdit:
              'https://corporate.orak365.net/SmartRegistry/Users/GetTestRepeater',
          }

      try {
        const { data } = await this.axios.get(apiUrl.jsonUrlControls)
        this.formData = data

        // get repeater form template, if there is repeater object
        if (
          !this.$lodash.isEmpty(
            this.formData.filter((data) => data.type === 'repeater')
          )
        ) {
          this.assignTemplateIds()
          this.getInitFormTemplate()
        }
      } catch (e) {
        console.error('Error loading data from the server:', e)
        this.error = true
        this.errorMessage = e.errorMessage
        setTimeout(() => this.resetError(), 5000)
      }
    },
    async submitForm() {
      try {
        this.resetError()

        if (this.hasRepeaterTemplates) {
          let formattedFormData = this.$lodash
            .cloneDeep(this.formData)
            .filter((data) => data.type === 'repeater')[0].values

          if (!this.$lodash.isEmpty(formattedFormData)) {
            for (let i = 0; i < formattedFormData.length; i++) {
              let result = this.$lodash.omit(formattedFormData[i], 'templateId')
              formattedFormData[i] = result
            }
          }

          this.formData.filter((data) => data.type === 'repeater')[0].values =
            formattedFormData
        } else {
          if (
            !this.$lodash.isEmpty(
              this.formData.filter((data) => data.type === 'repeater')
            )
          ) {
            this.formData.filter((data) => data.type === 'repeater')[0].values =
              this.repeaterForm
          }
        }

        console.log('submitted data:', this.formData)
        const apiUrl = !this.$lodash.isEmpty(window.jsonData)
          ? window.jsonData
          : {}

        const { data } = await this.axios.post(
          apiUrl.jsonUrlCreateEdit,
          this.formData
        )

        if (data.status === 'Error' || data.status === 'error') {
          this.error = true
          this.errorMessage = data.errorMessage
          setTimeout(() => this.resetError(), 5000)
        } else if (data.status === 'Success' || data.status === 'success') {
          location.assign(data.redirectUrl)
        }
      } catch (e) {
        console.log('Error submitting form', '\n', e)
        if (!this.$lodash.isEmpty(e.errorMessage)) {
          this.error = true
          this.errorMessage = e.errorMessage
        }
        setTimeout(() => this.resetError(), 5000)
      }
    },
    assignTemplateIds() {
      let repeaterObject = this.$lodash.cloneDeep(
        this.formData.filter((data) => data.type === 'repeater')[0]
      )
      // add inner templateId for every object from the repeater template,
      // except the init form template
      if (!this.$lodash.isEmpty(repeaterObject.values)) {
        let mappedTemplates = repeaterObject.values

        mappedTemplates = mappedTemplates.map((obj) => {
          this.counter += 1
          return {
            ...(() => {
              if (!this.$lodash.has(obj, 'templateId')) {
                return { templateId: this.counter.toString() }
              }
            })(),
            ...obj,
          }
        })

        this.formData.filter((data) => data.type === 'repeater')[0].values =
          mappedTemplates

        this.repeaterTemplates = mappedTemplates
      }
    },
    getInitFormTemplate() {
      let initForm = this.$lodash.cloneDeep(
        this.formData.filter((data) => data.type === 'repeater')[0].itemTemplate
      )
      if (!this.$lodash.isEmpty(initForm)) {
        this.repeaterForm = this.$lodash.cloneDeep(initForm)
        this.cleanRepeaterForm = this.$lodash.cloneDeep(initForm)
      }
    },
    createTemplate() {
      this.formData
        .filter((data) => data.type === 'repeater')[0]
        .values.push(this.cleanRepeaterForm)

      this.assignTemplateIds()
    },
    createFromInit({ template }) {
      this.formData
        .filter((data) => data.type === 'repeater')[0]
        .values.push(template)

      this.formData
        .filter((data) => data.type === 'repeater')[0]
        .values.push(this.cleanRepeaterForm)

      this.assignTemplateIds()
    },
    updateTemplate(template) {
      this.formData
        .filter((data) => data.type == 'repeater')[0]
        .values.filter((item) => item.templateId === template.templateId)[0] =
        template
    },
    deleteTemplate(template) {
      let index = this.formData
        .filter((data) => data.type == 'repeater')[0]
        .values.findIndex((item) => item.templateId == template.templateId)

      this.formData
        .filter((data) => data.type == 'repeater')[0]
        .values.splice(index, 1)

      this.assignTemplateIds()
    },
    setInitTemplate({ template }) {
      this.repeaterForm = this.$lodash.cloneDeep(template)
    },
    btnClass(type) {
      switch (type) {
        case 'add':
          return 'btn btn-warning'
        case 'history':
          return 'btn btn-info'
        case 'edit':
          return 'btn btn-warning'
        case 'delete':
          return 'btn btn-danger'
      }
    },
    btnIconClass(type) {
      switch (type) {
        case 'add':
          return 'mdi mdi-eye-outline'
        case 'history':
          return 'mdi mdi-progress-clock'
        case 'edit':
          return 'dripicons-pencil'
        case 'delete':
          return 'dripicons-trash'
      }
    },
    resetError() {
      this.error = false
      this.errorMessage = ''
    },
  },
}
</script>
