<template>
  <div v-if="hasTemplateData" class="pb-3" @submit.prevent>
    <div v-for="(form, i) in template.controls" :key="i" class="row bg-light">
      <label :for="form.name" class="col-2">
        <p v-if="form.type !== 'checkbox'">
          {{ form.label }}
        </p>
      </label>
      <div class="col-10">
        <template v-if="form.type === 'password'">
          <input
            :id="form.name"
            v-model.trim.lazy="form.value"
            class="form-control input-password"
            :type="form.type"
            :required="form.required"
            :name="form.name"
            :disabled="!form.enabled"
          />
          <a href="javascript:void(0)" class="show-pass"
            ><i class="fa fa-eye-slash"></i
          ></a>
        </template>
        <template v-if="form.type === 'date'">
          <input
            :id="form.name"
            v-model.trim.lazy="form.value"
            class="form-control"
            :type="form.type"
            :required="form.required"
            :name="form.name"
            :disabled="!form.enabled"
          />
        </template>
        <template v-if="form.type === 'datetime'">
          <datetime
            v-model.trim.lazy="form.value"
            :type="form.type"
            input-class="form-control"
            :input-id="form.name"
            format="MM/dd/yyyy HH:MM a"
            autocomplete="off"
            :disabled="!form.enabled"
          ></datetime>
        </template>
        <template v-if="form.type === 'paragraph'">
          <input
            :id="form.name"
            v-model.trim="form.value"
            :type="form.type"
            class="form-control"
            :required="form.required"
            readOnly="true"
            :name="form.name"
            :disabled="!form.enabled"
          />
        </template>
        <template v-if="form.type === 'text'">
          <input
            :id="form.name"
            v-model.trim="form.value"
            :type="form.type"
            class="form-control"
            :required="form.required"
            :name="form.name"
            :disabled="!form.enabled"
          />
        </template>
        <template v-if="form.type === 'textarea'">
          <textarea
            :id="form.name"
            v-model.trim="form.value"
            class="form-control"
            :required="form.required"
            :name="form.name"
            :rows="form.rows"
            :cols="form.cols"
            :disabled="!form.enabled"
          ></textarea>
        </template>
        <template v-if="form.type === 'checkbox'">
          <b-form-checkbox
            :id="form.name + Date.now()"
            v-model.lazy="form.value"
            :name="form.name"
            :disabled="!form.enabled"
          >
            {{ form.label }}
          </b-form-checkbox>
        </template>
        <template v-if="form.type === 'select'">
          <multiselect
            v-model.lazy="form.selectedValue"
            :options="form.options"
            :searchable="false"
            :show-labels="false"
            label="label"
            track-by="label"
            :disabled="!form.enabled"
          ></multiselect>
        </template>
        <template v-if="form.type === 'selectsearch'">
          <multiselect
            v-model.lazy="form.value"
            :options="form.options"
            :custom-label="nameWithLang"
            label="label"
            track-by="label"
            :disabled="!form.enabled"
          ></multiselect>
        </template>
        <template v-if="form.type === 'multiselect'"
          ><multiselect
            v-model="form.selectedValues"
            :options="form.options"
            :multiple="true"
            :close-on-select="false"
            :preserve-search="true"
            label="label"
            track-by="label"
            :reset-after="false"
            group-values="groupOptions"
            group-label="groupLabel"
            :group-select="true"
            :hide-selected="true"
            :disabled="!form.enabled"
          ></multiselect>
        </template>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <div class="d-flex justify-content-end">
          <button class="btn btn-warning text-white" @click.prevent="create">
            <i class="mdi mdi-plus-box-multiple-outline"></i>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'InitFormTemplate',
  props: {
    initFormTemplate: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      template: this.$lodash.cloneDeep(this.initFormTemplate),
    }
  },
  computed: {
    hasTemplateData() {
      return !this.$lodash.isEmpty(this.initFormTemplate)
    },
  },
  watch: {
    template: {
      handler() {
        this.$emit('set-init-template', { template: this.template })
      },
      deep: true,
    },
  },
  methods: {
    create() {
      this.$emit('create', { template: this.template })
    },
  },
}
</script>
